import Scroller from "vux/src/components/scroller/index";
import http from "@/axios"
import BScroll from 'better-scroll'
export default {
    components: {Scroller},
    name:"join_activity_member_list",
    props:['result'],
    data(){
        return{
            activityId:this.$route.query.id,
            memberList:[],
            hasMore:true,
            pageNo:1,
            scrollerStatus: {
                pullupStatus: 'default'
            },
            totalAmount:0,//总金额
            total:0,//总人数
            isReleased:false,//下拉刷新是否释放
            isPullup:false,//是否处于上拉加载状态
            isMore:true,//是否有更多数据
            // upobj: {
            //     content: '请上拉刷新数据',
            //     pullUpHeight: 60,
            //     height: 40,
            //     autoRefresh: false,
            //     downContent: '请上拉刷新数据',
            //     upContent: '请上拉刷新数据',
            //     loadingContent: '<span style="color: red">加载中...</span>',
            //     clsPrefix: 'xs-plugin-pullup-'
            // },
        }
    },
    mounted(){
        this.getArticleList();
        this.$nextTick(() => { 
            this.scroll = new BScroll(this.$refs.wrapper,{
                click:true,//允许点击子元素
                probeType: 3,
                pullDownRefresh: {
                    threshold: 50,
                    stop: 40
                },
                pullUpLoad: {
                    threshold: 0
                }
            })
            this.scroll.on('pullingDown',()=>{
                console.log('下拉刷新')
                this.isReleased = true
                this.pageNo =1
                this.getArticleList()

            })

            this.scroll.on('pullingUp',()=>{
                console.log('上拉加载')
                this.isPullup = true
                if(this.memberList.length === this.total){
                    this.isMore = false
                }
                console.log(this.memberList.length,this.totalAmount,this.isMore)
                this.pageNo += 1
                this.getArticleList()
            })
        })
    },
    methods:{
        getArticleList(){
            var current = (this.pageNo - 1) * 15;
            var self = this;
            http.getMoneyUserList({
                id:this.activityId,
                currentResult:current,
                pageSize:15
            },function (response) {
                if(current==0){
                    self.totalAmount = response.total * response.data[0].amount
                    self.total = response.total
                }
                if(response.total > (current + 15)){
                    self.scrollerStatus.pullupStatus = 'default'
                    self.hasMore = true;
                }else{
                    self.scrollerStatus.pullupStatus = 'disabled'
                    self.hasMore = false;
                }

                if(self.pageNo == 1){
                    self.memberList = [];
                }
                self.memberList = self.memberList.concat(response.data);
                self.$nextTick(function () {
                    self.scroll.refresh();
                    self.scroll.finishPullUp()
                    self.scroll.finishPullDown()
                    // self.$refs.scroller.donePulldown()
                    self.isReleased = false
                    self.isPullup = false
                })
            })
        }
    },
    
}



